import React, { Component } from "react";
import "./ITEM.css";

import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Rating from "@material-ui/lab/Rating";

import { CartContext } from "./contexts/CartContext";
import Popup from "react-popup";
//function Item(props) {
class Item extends Component {
  state = {};
  static contextType = CartContext;
  render() {
    var id = this.props.id;
    var title = this.props.title;
    var rating = this.props.rating;
    var img = this.props.img;
    var price = this.props.price;
    var desc = this.props.desc;
    var cmnts_counto = this.props.cmnts_count;
    
    var nf = new Intl.NumberFormat();
    var cmnts_count = nf.format(cmnts_counto); // "1,234,567,890"
const item_details={
  id:id,
  title:title,
  img:img,
  price:price,
  desc:desc,
  rating:rating,
  desc:desc,
  

}
    return (
      <div className="item">
        {/*}
      <h4>{title}</h4>
      <h5>
        {rating}stars
        <i class="fa fa-star-o"></i>
      </h5>
      
  */}
        <img src={img} className="p_img"></img>
        <p className="desc" style={{fontSize:"90%"}}>{desc}</p>
        <div className="below">
        <div className="r_p">
          <div className="cmnts">
            <Rating name="half-rating" defaultValue={rating} precision={1} />
            <p>{cmnts_count}</p>
          </div>
          <div className="cmnts r_p" >
            <h5 className="price">${price}</h5>
            <img src="prime.JPG" className="pimage"></img>
          </div>
          
          <button
            className="cart_button"
            onClick={() => {
              this.context.add_cart(item_details);
                //alert("Item added to cart")
              console.log("btn clciked");
            }}
          >
            <pre>Add to cart</pre>
          </button>
          
          </div>
        </div>
     
      </div>
    );
  }
}
export default Item;
