import React, { Component } from 'react';
import "./itemInCart.css"
import { CartContext } from "./contexts/CartContext";

class ItemInCart extends Component {
    state = {  }
    static contextType = CartContext;
    render() { 
        //console.log("in item in cart comp",this.props)
        return ( 
            <div className="background">
            <div className="item_in_cart">
            {/*}
             {console.log("itemincart",this.props.param)}
             <h2>{this.props.param.id} {this.props.param.title}  {this.props.param.img}  {this.props.param.price}  {this.props.param.rating} ...........................</h2>
             {this.props.param.desc}
        */}
       
        <img className="img" src={this.props.param.img}></img>
       <span className="span_cls"></span>
                <div className="data">
                <p className="desc">{this.props.param.desc}</p>
               
                <p className="price">  &#x20B9;{this.props.param.price}</p>
                <button
                className="cart_button" onClick={()=>{
                 
                  this.context.remove_item_from_cart(this.props.param)
            
                }}>
                remove from cart
              </button>





                    
                </div>
                </div>
                </div>
         );
    }
}
 
export default ItemInCart;