import React, { Component } from "react";

import { CartContext } from "./contexts/CartContext";

import ItemInCart from "./ItemInCart";
class CartList extends Component {
  state = {};
  static contextType = CartContext;
  render() {
    // this.context.user_cart.map((item) => {
    //   console.log(item);
    // });
if (this.context.user_cart.length==0){
  return (
    <div>
    <h1 style={{marginLeft:"10%"}}>Shopping cart</h1>
    <h3 style={{marginLeft:"10%",color:"red",marginTop:"20px"}}>cart is empty,please add products to yours cart</h3>
    </div>
  );
}
    return (
      <div className="cart_list">
      <div className="amt">
      <p>
      <pre style={{color:"green"}}>
      Part of your order qualifies for <br></br>
      FREE Delivery. 
      
      </pre>
      </p>
      <p style={{fontFamily:"Aerial",fontSize:"20px",}}>Subtotal ({this.context.user_cart.length} items): <span style={{fontFamily:"Aerial",fontSize:"20px",fontWeight:"bold"}}> &#x20B9;{this.context.cart_cost}</span>   </p>
      <p>This order contains a gift</p>
      <button className="proceed_to_buy">Proceed to buy</button>
      </div>
      <div className="cart_products">
        <h1 style={{marginLeft:"10%"}}>Shopping cart</h1>
       

        {/* <ItemInCart items={this.context.user_cart}></ItemInCart>*/}
        {this.context.user_cart.map((item) => (
          <ItemInCart key={item.id} param={item}></ItemInCart>
        ))}
        </div>
      </div>
    );
  }
}

export default CartList;
